/* Navigation
--------------------------------------------- */
.main-navigation {
  clear: both;
  display: block;
  float: left;
  width: 100%; }
  .main-navigation ul {
    display: none;
    list-style: none;
    margin: 0;
    padding-left: 0; }
    .main-navigation ul ul {
      box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
      float: left;
      position: absolute;
      top: 1.5em;
      left: -999em;
      z-index: 99999; }
      .main-navigation ul ul ul {
        left: -999em;
        top: 0; }
      .main-navigation ul ul li:hover > ul,
      .main-navigation ul ul li.focus > ul {
        left: 100%; }
    .main-navigation ul li:hover > ul,
    .main-navigation ul li.focus > ul {
      left: auto; }
  .main-navigation li {
    float: left;
    position: relative; }
  .main-navigation a {
    display: block;
    text-decoration: none; }

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
  display: block; }

@media screen and (min-width: 650px) {
  .menu-toggle {
    display: none; }
  .main-navigation ul {
    display: block; } }

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
  margin: 0 0 1.5em;
  overflow: hidden; }

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  float: left;
  width: 50%; }

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  float: right;
  text-align: right;
  width: 50%; }

.nav-links .nav-previous {
  margin-left: 20px; }

.nav-links .nav-next {
  margin-right: 20px; }

.nav-links .nav-previous, .nav-links .nav-next {
  background-color: #a3620d;
  width: 190px;
  max-width: 40%;
  font-size: 1em;
  line-height: 1.3em; }
  .nav-links .nav-previous:hover, .nav-links .nav-next:hover {
    background-color: #eb6222; }
  .nav-links .nav-previous a, .nav-links .nav-next a {
    display: block;
    padding: 12px 20px;
    color: #fff; }
    .nav-links .nav-previous a:hover, .nav-links .nav-previous a:active, .nav-links .nav-previous a:focus, .nav-links .nav-next a:hover, .nav-links .nav-next a:active, .nav-links .nav-next a:focus {
      text-decoration: none; }

.nav-links .post-prev-container, .nav-links .post-next-container {
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 100%;
  display: block;
  overflow: hidden; }

/* Posts and pages
--------------------------------------------- */
.sticky {
  display: block; }

.post,
.page {
  margin: 0 0px 3.5em; }

.updated:not(.published) {
  display: none; }

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5em 0 0; }

.page-links {
  clear: both;
  margin: 0 0 1.5em; }

.comments-link a {
  margin-left: 20px; }

.edit-link a {
  margin-left: 20px; }

.entry-title {
  text-align: center; }

.entry-footer {
  color: #666;
  font-size: 0.9rem; }
  .entry-footer a:hover {
    color: #a3620d; }

.entry-meta {
  color: #666;
  font-size: 0.9rem; }
  .entry-meta a {
    font-size: 0.9rem; }
  .entry-meta a:hover {
    color: #a3620d; }

.post-thumbnail img {
  margin-top: 15px; }

.wp-caption {
  width: 100% !important;
  text-align: center; }

/* Comments
--------------------------------------------- */
.comment-content a {
  word-wrap: break-word; }

.bypostauthor {
  display: block; }

/* Widgets
--------------------------------------------- */
.widget {
  margin: 0 0 1.5em; }
  .widget select {
    max-width: 100%; }

/* Media
--------------------------------------------- */
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
  display: inline-block; }

/* Captions
--------------------------------------------- */
figcaption {
  margin-bottom: 1.5em;
  max-width: 100%; }
  figcaption img[class*="wp-image-"] {
    display: block;
    margin-left: auto;
    margin-right: auto; }
  figcaption .wp-caption-text {
    margin: 0.8075em 0; }

.alignleft figcaption {
  text-align: left; }

.aligncenter figcaption {
  text-align: center; }

.alignright figcaption {
  text-align: right; }

.wp-caption-text {
  text-align: center; }

/* Galleries
--------------------------------------------- */
.gallery {
  margin-bottom: 1.5em;
  display: grid;
  grid-gap: 1.5em; }

.gallery-item {
  display: inline-block;
  text-align: center;
  width: 100%; }

.gallery-columns-2 {
